<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">设置</el-row>
    <el-row type="flex" justify="start" style="margin-top: 30px;">
     <el-form ref="form" :model="form" label-width="80px" label-suffix=":" label-position="left">
       <el-form-item label="资讯板块开启关闭" prop="information_enable" label-width="130px">
          <el-radio-group v-model="form.information_enable">
            <el-radio :label="true">开启</el-radio>
            <el-radio :label="false">关闭</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="分享文案" prop="share_content">
          <el-input v-model="form.share_content" placeholder="请输入分享文案" />
        </el-form-item>
        <el-form-item label="分享图片">
          <div v-if="form.share_picture && form.share_picture.url" class="avatar">
            <img :src="form.share_picture.url" />
            <i class="el-icon-circle-close" @click="removeConverImageUrl" />
          </div>
          <el-upload v-else class="avatar-uploader"
                    action="http://szebra.cn/api/admin/images"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    name="image[file]"
                    :before-upload="beforeAvatarUpload">
            <i class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-button type="primary" @click="onSubmit">保存</el-button>
      </el-form>
    </el-row>
  </div>
</template>
<script>

export default {
  components: {
  },

  data () {
    return {
      form: {
        information_enable: null,
        share_content: '',
        share_picture: { id: '', url: '' }
      }
    }
  },

  methods: {
    removeConverImageUrl () {
      this.form.share_picture.id = this.form.share_picture.url = ''
    },
    handleAvatarSuccess (data) {
      this.form.share_picture = data.result_rows
    },
    beforeAvatarUpload (file) {
      const isImg = file.type.startsWith('image/')
      const isLt2M = file.size / 1024 / 1024 < 5
      if (!isImg) {
        this.$message.error('只允许上传图片')
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 5MB!')
      }
      return isImg && isLt2M
    },
    // 提交表单
    onSubmit () {
      const data = { weapp_setting: Object.assign({}, this.form) }
      data.weapp_setting.share_picture = undefined
      data.weapp_setting.share_picture_attributes = this.form.share_picture
      this.$http.settingUpdate(data).then(res => {
        if (!res) return
        this.$message.success('保存成功')
      })
    }
  },
  created () {
    this.$http.getSettingDetail().then(res => {
      if (!res) return
      Object.assign(this.form, res.data.result_rows)
      if (!this.form.share_picture) this.form.share_picture = { id: '', url: '' }
    })
  }
}
</script>

<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    position: relative;
    float: left;
    margin-right: 5px;
  }
  .avatar img {
    margin-top: 6px;
    margin-right: 6px;
    width: 178px;
    height: 178px;
  }
  .avatar i {
    position: absolute;
    top: 0;
    right: 0;
    opacity: 0.6;
  }
  .avatar i:hover {
    opacity: 1;
    cursor: pointer;
  }
</style>
